.section-title {
  font-size: 1.6em;
  font-weight: bold;
  padding: 10px 15%;
}

.trash {
  height: 25px;
  width: 25px;
  position: absolute;
  left: 30px;
  top: 30px;
  z-index: 500;
  background-color: transparent;
  border: 0;
}
.trash img {
  width: 100%;
  height: 100%;
  opacity: 0.3;
}

.trash img:hover {
  opacity: 1;
}

.list-container {
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  height: 400px;
  overflow-y: scroll;
  margin: 0px 15%;
  background-color: #ffffff;
}

.boletin-row {
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  padding-left: 120px;
  height: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  cursor: pointer;
  text-decoration: none;
  color: #000;
  position: relative;
}

.boletin-row:hover {
  background-color: #f5f5f5;
}
.boletin-row span {
  color: #aaa;
  padding-top: 15px;
}

.boletin-row h2 {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding-top: 20px;
