  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.add-article-link {
  padding: 9px;
  border-radius: 6px;
  border: 2px solid #4286f4;
  margin: 25px -20px 25px 0px;
  background-color: #fff;
  position: relative;
  height: 40px;
  -ms-flex-item-align: center;
      align-self: center;
  display: -ms-flexbox;
  display: flex;
  text-decoration: none;
}

.add-article-link:hover {
  background-color: #4286f4;
}

.add-article-link:hover .add-article-btn {
  color: #fff;
}

.add-article-btn {
  text-decoration: none;
  color: #4286f4;
  background-color: transparent;
  font-weight: bold;
  border: 0;
  font-size: 12px;
  padding: 0;
  cursor: pointer;
  text-transform: uppercase;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-pack: center;
      justify-content: center;
}

