.add-boletin-btn button {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;

  font-size: 1em;
  text-transform: capitalize;
  font-family: "Lato", sans-serif;
}

.publish-btn {
  text-decoration: none;
  color: #e72e3d;
