.admin-footer {
  background-color: #fff;
  border-top: 1px solid #4286f4;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  bottom: 0;
  height: 70px;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  z-index: -1;
}

.admin-footer div {
  text-align: center;
  width: 270px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 60px;
}

.admin-footer div img:first-child {
  height: 100%;
}

.admin-footer div img:last-child {
  height: 40%;
}



