.route-title {
  font-size: 1.6em;
  font-weight: bold;
  padding: 10px 0px 0px 0px;
}

.boletin-input {
  width: 100%;
  border: 1px solid #ccc;
  font-size: 1.2em;
  padding: 10px 20px;
  margin-bottom: 5px;
  margin-top:10px;
}

.submit-boletin {
  background-color: #4286f4;
  color: #fff;
  padding: 20px 40px;
  border: 0;
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
}

.disabled-btn {
  background-color: #ccc;
  color: #fff;
  padding: 20px 40px;
  border: 0;
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: bold;
  margin-top: 10px;

}



