.route-tit {
  font-size: 1.6em;
  font-weight: bold;
  padding: 10px 0px;
}

.category-input {
  width: 100%;
  border: 1px solid #ccc;
  font-size: 1.2em;
  padding: 10px 20px;
  margin-bottom: 15px;
}

.color-input-label {
  font-size: 1.2em;
}
.category-input-color {
  width: 50%;
  border: 1px solid #ccc;
  height: 30px;
  margin-bottom: 25px;
  margin-top: 15px;
}

.submit-category {
  background-color: #4286f4;
  color: #fff;
  padding: 20px 40px;
  border: 0;
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}

.disabled-button {
  background-color: #ccc;
  color: #fff;
  padding: 20px 40px;
  border: 0;
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: bold;
}



