.title-input {
  width: 100%;
  padding: 10px 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 18px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
}

.add-new {
  border: solid 1.5px #4286f4;
  color: #4286f4;
  padding: 10px 16px;
  cursor: pointer;
  border-radius: 100px;
  background-color: #fff;
}

.add-new:focus {
  outline: none;
}

.image-input {
  width: 100%;
  padding: 10px 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  border: 1px solid #ccc;
  margin-bottom: 10px;
}

.image-input:first-child {
  margin-right: 10px;
}

.content {
  padding: 0px 15%;
}

.section-t {
  font-size: 1.6em;
  font-weight: bold;
  padding: 10px 0px;
}

.submit-row {
  margin: 30px 15% 80px 15%;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.no-submit {
  background-color: #ccc;
  padding: 20px;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  color: #fff;
  position: absolute;
  right: 0;
}

.submit-article {
  background-color: #4286f4;
  color: #fff;
  padding: 20px;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  right: 0;
}

.cat-select {
  border: 1px solid #ccc;
  position: relative;
  padding: 15px 30px 15px 15px;
  border-radius: 4px;
  cursor: pointer;
  width: 194.44px;
  background-color: #f5f5f5;
}

.cat-row {
  border-bottom: 1px solid #ccc;
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
  border-top: 0;
  padding: 15px 30px 15px 15px;
  cursor: pointer;
  background-color: #f5f5f5;
}

.cat-row:hover {
  background-color: #fff;
}

.cat-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: absolute;
  left: 0;
  background-color: #f5f5f5;
}

.cat-select img {
  height: 19px;
  width: 13px;
  position: absolute;
  right: 8px;
  top: 13px;
}

.editor {
  height: 150px;
  margin-bottom: 20px;
  background-color: #fff;
}



