.category-container {
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  height: 400px;
  overflow-y: scroll;
  margin: 0px 15%;
  background-color: #ffffff;
}

.add-categoria-container {
  padding: 0px 15%;
  position: relative;
  margin-bottom: 80px;
}

.add-categoria-btn {
  background-color: #4286f4;
  margin-top: 15px;
  border: 0;
  padding: 10px 20px;
  border-radius: 4px;
  position: absolute;
  right: 15%;
  cursor: pointer;
}

.add-categoria-btn button {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;

  font-size: 1em;
  text-transform: capitalize;
  font-family: "Lato", sans-serif;
}



