.toolbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}

.header-btn {
  text-decoration: none;
  color: none;
  background-color: transparent;
  border: 0;
  font-size: 15px;
  cursor: pointer;
  text-transform: uppercase;
  color: #999;
}

.header-btn:focus {
  outline: none;
}

.header-link {
  padding: 10px;
}

.header-link:hover .header-btn {
  color: #4286f4;
}

.header-link:hover {
  background-color: #f5f5f5;
  border-bottom: 3px solid #4286f4;
}

.activeTab {
  background-color: #f5f5f5;
  border-bottom: 3px solid #4286f4;
}

.activeTab .header-btn {
  color: #4286f4;
}



