.article-list-container {
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  height: 400px;
  overflow-y: scroll;
  margin: 0px 15%;
  position: relative;
  background-color: #ffffff;
}

.article-row {
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  padding-left: 120px;
  height: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
}

.input-box {
  -ms-flex-item-align: center;
      align-self: center;
}

.input-box form {
  width: 100px;
  margin-right: 130px;
}
.input-el {
  opacity: 0;
  z-index: -1;
  width: 0;
  position: absolute;
}

.upload-audio {
  background-color: lightcoral;
  font-weight: bold;
  color: #fff;
  border: 1px solid lightcoral;
  padding: 15px 5px;
  width: 120px;
  border-radius: 6px;
  font-size: 16px;
}

label {
  cursor: pointer;
  padding: 15px 5px;
  background-color: lightseagreen;
  width: 120px;
  position: absolute;
  top: 0;
  margin-top: 23px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  font-size: 16px;
  border-radius: 6px;
  border: 2px solid lightseagreen;
  /* Style as you please, it will become the visible UI component. */
}
.article-row-link {
  cursor: pointer;
  text-decoration: none;
  color: #000;
}

.article-row:hover {
  background-color: #f5f5f5;
}

.file-span {
  padding: 0;
  color: #000;
  margin-top: -20px;
  text-align: center;
  position: absolute;
}

.article-span {
  color: #aaa;
  padding-top: 14px;
}

.article-row h3 {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding-top: 17px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}



