.delete-film {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}

#nojoda {
  background-color: #fff;
  margin-top: 15px;
  color: #4286f4;
  padding: 10px 20px;
  cursor: pointer;
  border: 2px solid #4286f4;
  font-weight: bold;
  margin-left: 40px;
}

.delete-film div {
  position: absolute;
  width: 400px;
  height: 200px;
  padding: 20px;
  top: 50%;
  left: 50%;
  margin: -250px 0 0 -200px;
  background-color: #fff;
  border-radius: 6px;
  text-align: center;
  font-size: 1.5em;
  padding-top: 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.delete-film div button {
  background-color: #4286f4;
  margin-top: 15px;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  border: 1px solid #fff;
  font-weight: bold;
}



